:root {
  --basic-white: #ffffff;
  --basic-red: #d22856;
  --basic-black: #000000;
  --basic-blue-dark: #283645;
  --basic-blue-darken: #1d2631;
  --basic-grey: #f2f2f2;
  --basic-grey-dark: #e5e5e5;
  --basic-green: #2aab6d;
  --basic-green-dark: #1c9b5e;
  --special-black-opacity: rgba(0, 0, 0, 0.3);
  --special-grey-opacity: rgba(0, 1, 1, 0.15);
  --special-white-opacity: rgba(255, 255, 255, 0.3);
  --special-whiten-opacity: rgba(255, 255, 255, 0.15);
  --special-red-opacity: rgba(210, 40, 86, 0.3);
  --special-blue-opacity: rgba(40, 54, 69, 0.3);
  --special-blue-light-opacity: rgba(40, 54, 69, 0.1);
}
@font-face {
  font-family: "Open Sans";
  src: local("Open Sans"), url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Open Sans";
  src: local("Open Sans Bold"), url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Open Sans";
  src: local("Open Sans Light"), url("../fonts/opensanslight.woff2") format("woff2"), url("../fonts/opensanslight.woff") format("woff");
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  white-space: nowrap;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
  overflow: hidden;
}
.wrapper {
  margin: 0 auto;
  padding: 0 20px;
  box-sizing: border-box;
}
@media (min-width: 970px) {
  .wrapper {
    max-width: 970px;
    padding: 0 15px;
  }
}
.page-body {
  position: relative;
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  min-width: 320px;
  font-family: "Open Sans", "Arial", sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 14px;
  line-height: 24px;
  text-align: center;
  box-sizing: border-box;
  color: var(--basic-black);
  background-color: var(--basic-white);
  padding: 0;
  margin: 0 auto;
}
.main-header {
  position: relative;
  z-index: 2;
  color: var(--basic-white);
  background-color: var(--basic-blue-dark);
  text-align: left;
}
.main-header__logo {
  min-height: 66px;
  box-sizing: border-box;
  padding: 22px 20px 14px 20px;
}
.main-header--opacity {
  background-color: var(--special-black-opacity);
}
@media (min-width: 660px) {
  .main-header__logo {
    min-height: 110px;
    padding-top: 30px;
  }
}
@media (min-width: 970px) {
  .main-header {
    background-color: var(--special-black-opacity);
  }
  .main-header__logo {
    padding-left: 0;
  }
  .main-header__wrapper {
    max-width: 970px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    box-sizing: border-box;
    padding: 0 15px;
    margin: 0 auto;
  }
}
.logo--boxshadow {
  box-shadow: inset 0 -1px 0 var(--basic-black);
}
.logo__link {
  position: relative;
  display: inline-block;
  vertical-align: top;
  padding: 0;
  margin: 0;
}
.logo__link--header {
  fill: var(--basic-white);
  color: var(--basic-white);
}
.logo__link--footer {
  text-align: center;
  color: var(--basic-red);
  fill: var(--basic-blue-dark);
}
.logo__link:not([href]) {
  pointer-events: none;
}
.logo__link:hover:not(:active),
.logo__link:focus:not(:active) {
  fill: var(--basic-red);
  color: var(--basic-red);
}
.logo__link:active {
  opacity: 0.3;
}
.logo__link--header .logo__img--tablet,
.logo__link--header .logo__img--desktop,
.logo__link--footer .logo__img--desktop {
  display: none;
}
@media (min-width: 660px) {
  .logo__link--header .logo__img--mobile {
    display: none;
  }
  .logo__link--header .logo__img--tablet {
    display: block;
  }
}
@media (min-width: 970px) {
  .logo--boxshadow {
    box-shadow: none;
  }
  .logo__link--header .logo__img--tablet {
    display: none;
  }
  .logo__link--header .logo__img--desktop {
    display: block;
  }
  .logo__link--footer .logo__img--mobile {
    display: none;
  }
  .logo__link--footer .logo__img--desktop {
    display: block;
  }
}
.main-nav__toggle {
  position: absolute;
  top: 6px;
  right: 15px;
  z-index: 2;
  width: 60px;
  height: 60px;
  display: block;
  background-color: transparent;
  border: none;
  padding: 0;
  cursor: pointer;
}
.main-nav--open .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 15px;
  right: 5px;
  width: 50px;
  height: 4px;
  background-color: var(--basic-white);
  box-shadow: 0 10px 0 0 var(--basic-white), 0 20px 0 0 var(--basic-white);
  border-radius: 5px;
}
.main-nav--open .main-nav__toggle:hover::before,
.main-nav--open .main-nav__toggle:focus::before {
  background-color: var(--basic-red);
  box-shadow: 0 10px 0 0 var(--basic-red), 0 20px 0 0 var(--basic-red);
}
.main-nav--open .main-nav__toggle:active::before {
  background-color: var(--special-red-opacity);
  box-shadow: 0 10px 0 0 var(--special-red-opacity), 0 20px 0 0 var(--special-red-opacity);
}
.main-nav--close .main-nav__toggle::before,
.main-nav--close .main-nav__toggle::after {
  position: absolute;
  content: "";
  top: 25px;
  right: 2px;
  width: 30px;
  height: 4px;
  background-color: var(--basic-white);
}
.main-nav--close .main-nav__toggle::before {
  transform: rotate(45deg);
}
.main-nav--close .main-nav__toggle::after {
  transform: rotate(-45deg);
}
.main-nav--close .main-nav__toggle:hover::before,
.main-nav--close .main-nav__toggle:focus::before,
.main-nav--close .main-nav__toggle:hover::after,
.main-nav--close .main-nav__toggle:focus::after {
  background-color: var(--basic-red);
}
.main-nav--close .main-nav__toggle:active::before,
.main-nav--close .main-nav__toggle:active::after {
  background-color: var(--special-red-opacity);
}
.main-nav__list {
  text-align: center;
  list-style: none;
  padding: 0;
  margin: 0;
}
.main-nav__item {
  display: block;
  padding: 0;
  margin: 0;
}
.main-nav__link {
  display: block;
  font-weight: bold;
  font-size: 18px;
  line-height: 15px;
  color: var(--basic-white);
  text-decoration: none;
  text-transform: uppercase;
  box-shadow: inset 0 -1px 0 var(--basic-black), inset 0 1px 0 var(--special-whiten-opacity);
  padding: 25px 0;
  margin: 0;
}
.main-nav__link:hover,
.main-nav__link:focus {
  color: var(--basic-red);
}
.main-nav__link:active {
  color: var(--special-red-opacity);
}
@media (min-width: 660px) {
  .main-nav__toggle {
    top: 30px;
  }
}
@media (max-width: 969px) {
  .main-nav--no-js .main-nav__toggle {
    display: none;
  }
  .main-nav--open .main-nav__item {
    display: none;
  }
}
@media (min-width: 970px) {
  .main-nav__toggle {
    display: none;
  }
  .main-nav__list {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
    padding-top: 18px;
  }
  .main-nav__list .main-nav__item {
    margin: 0 40px 0 40px;
  }
  .main-nav__list .main-nav__item:last-of-type {
    margin-right: 0;
  }
  .main-nav__link {
    font-weight: normal;
    font-size: 18px;
    line-height: 18px;
    text-transform: none;
    box-shadow: none;
    padding-bottom: 13px;
  }
  .main-nav__link:active {
    color: var(--special-white-opacity);
  }
  .main-nav__link--active {
    border-bottom: 2px solid var(--basic-white);
    pointer-events: none;
  }
}
