.range {
  max-width: 280px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  margin: 0 auto;
}
.range__top {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  position: relative;
  padding-bottom: 70px;
  padding-left: 37px;
}
.range__label {
  margin: 13px 35px 0 0;
}
.range__label::after {
  position: absolute;
  content: "";
  width: 320px;
  height: 1px;
  background-color: var(--special-blue-light-opacity);
  bottom: 2px;
  left: -21px;
}
.range__label--fill::after,
.range__label--contrast::after {
  display: none;
}
.range__img {
  fill: var(--basic-blue-dark);
  opacity: 0.3;
}
.range__img:hover,
.range__img:focus {
  opacity: 0.5;
}
.range__img--active {
  fill: var(--basic-red);
  opacity: 1;
}
.range__img--active:hover,
.range__img--active:focus {
  opacity: 1;
}
.range__label-button {
  width: 44px;
  height: 44px;
  background-color: transparent;
}
.range__toggle {
  display: none;
  appearance: none;
  /* Hides the slider so that custom slider can be made */
  background: transparent;
  /* Otherwise white in Chrome */
  min-width: 240px;
  height: 2px;
  cursor: pointer;
  box-sizing: border-box;
  box-shadow: inset 1px 2px 1px #000000;
  position: absolute;
  bottom: 35px;
  left: 18px;
}
.range__toggle--crop {
  display: block;
}
.range__toggle::-ms-track {
  width: 100%;
  cursor: pointer;
  /* Hides the slider so custom styles can be added */
  background: transparent;
  border-color: transparent;
  color: transparent;
}
/* Special styling for WebKit/Blink */
.range__toggle::-webkit-slider-thumb {
  appearance: none;
  border: 2px solid var(--basic-red);
  height: 14px;
  width: 14px;
  border-radius: 50%;
  background: #ffffff;
  cursor: pointer;
}
.range__toggle::-webkit-slider-thumb:active {
  background-color: var(--basic-red);
}
/* All the same stuff for Firefox */
.range__toggle::-moz-range-thumb {
  border: 2px solid var(--basic-red);
  height: 14px;
  width: 14px;
  border-radius: 50%;
  background: #ffffff;
  cursor: pointer;
}
.range__toggle::-moz-range-thumb:active {
  background-color: var(--basic-red);
}
